// при загрузке загружаеться эти стили , принажатии на home тоже
.overlay-header {
    height: 230px;
    margin-left: auto;
    margin-right: auto;
    background-size: cover;
    background-position: center;
    background-image: url('../images/header/desktop/header1x.png');

    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        background-image: url('../images/header/desktop/header2x.png');
    }

    @media screen and (min-width: 321px) and (max-width: 768px) {
        background-image: url('../images/header/tablet/header@1x.png');
    }

    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        background-image: url('../images/header/tablet/header@2x.png');
    }
}

@media screen and (max-width: 320px) {
    .overlay-header {
        height: 230px;
        margin-left: auto;
        margin-right: auto;
        background-size: cover;
        background-position: center;
        background-image: url('../images/header/mobile/header@1x.png');
    }

    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        .overlay-header{
            height: 230px;
            margin-left: auto;
            margin-right: auto;
            background-size: cover;
            background-position: center;
            background-image: url('../images/header/mobile/header@2x.png');
        }
    }
}
//==================================================================

.navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__logo {
        display: flex;
        align-items: baseline;
        cursor: pointer;
          align-items: center
}
    &__logo_icon {
        display: block;
        width: 24px; 
        height:24px;
        stroke:white;
        cursor: pointer;
 }
    &__title {
        display: none;
        margin-left: 10px;
        font-weight: 500;
        font-size: 30px;
        line-height: 35px;
        color: $title-color;

        @media screen and (min-width: 768px) {
            
            display: block;
        }
    }

    &__list {
        display: flex;
        &--item {
                padding: 0;
                &:not(:last-child) {
                    margin-right: 39px;
                }
                &--link{
                    color: $title-color; 
                    text-transform: uppercase;
                }
            }

    }
    

    
   
}
//Узнатьу колег 
.search {
    // width: 100%;
    width: 394px;
    margin-top: 40px;
    position: relative;
     margin: 0 auto;
    margin-top: 41px;
    @media screen and (max-width: 1280px) {
        width: 336px;
    }

    @media screen  and (max-width: 768px){
        margin-top: 57px;
     width: 280px;
          }
     @media screen and (max-width: 320px)  {
       width: 100%;
    }


    &_input {
        width: 100%;
        border: none;
        border-bottom: 0.5px solid $title-color;
        background-color: transparent;
        outline: none;
        color: $title-color;

        &::placeholder {
            color: $title-color;
        }
    }
// 245EBE393931
    &_btn {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        border: 0;
        cursor: pointer;
        background-color: transparent;
    }
    &_svg {
        width: 12px;
        height: 12px;
    }

}

.search_error {
    margin-top: 20px;
        font-size: 14px;
        line-height: 1.17;
        color:$erorr-text-color;
        text-align: center;
}

.navigation_list--active {
    position: relative;

    &::after {
        content: '';
    
        position: absolute;
        left: 0;
        bottom: -1px;
        background-color: $accent-color;
        border-radius: 2px;
        width: 100%;
        height: 3px;
    }
}
// для ошибки в поиске js
.hide {
    display: none;
}

.show {
    display: block;
}